export default {
  props: ["base", "home", "dir"],
  data() {
    return {
      viewAcc: true,
      boston: true,
      adv: false,
      isActive: false,
      awash: true,
    };
  },
  methods: {
    addClass() {
      this.isActive = true;
    },
  },
  mounted() {
    if (this.home == "waterpark/" || this.home == "/waterpark") {
      this.viewAcc = false;
    } else if (this.home == "boston/" || this.home == "/boston") {
      this.boston = false;
    }

    if (this.dir == "Entoto") {
      this.adv = true;
    }
    if (this.dir == "Awash") {
      this.awash = false;
    }
  },
};
